export const modelItemToBoolean = (value: string | boolean): boolean => {
  if (typeof value === 'boolean') {
    return value;
  }

  return value.trim() === 'true';
};

export const modelItemToArray = (value: string): string[] => value.replace(/\s/g, '').split(',');

export const secondsToMilliseconds = (seconds: number) => seconds * 1000;
